.current {
    color: green;
  }


.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 400px));
  grid-row-gap: 1rem;
}
.grid > div {
  background: #fafafa;
  padding: 1rem;
  border-radius: 1rem;
}

.section {
  align-content: center;
}

.column {
  #a {
    color: black;
    text-decoration: none;
  } 
}
